import React from 'react'
import { Container } from 'react-bootstrap'
import "../Homepage/Homepage.style.css"  //   import
import CharacterSearch from './components/CharacterSearch'  //    import
import Todaygrade from './components/Todaygrade'  //    import
import { useNavigate } from 'react-router-dom'  //    useNavigate  import

// Homepage  
const Homepage = ({selectedName, selectedServer, selectedServerId, setSelectedName, setSelectedServer, setSelectedServerId}) => {

  const navigate = useNavigate();  //    navigate  
  
  //    
  const gohome = () => {
    navigate('/');  // '/'  
  }

  return (
    <div className='homepage'>
      <Container>  {/*     */}
      
        {/*        */}
        <div className="nav-section" onClick={gohome}>
          <img src='./img/logoDNF.png' alt="DNF Logo" />  {/*   */}
        </div>

        {/*     */}
        <div className="search-section">
          <CharacterSearch 
            selectedServer={selectedServer} 
            setSelectedServer={setSelectedServer}
            selectedServerId={selectedServerId} 
            setSelectedServerId={setSelectedServerId}
            selectedName={selectedName} 
            setSelectedName={setSelectedName}
          />
        </div>

        {/*  */}
        <hr style={{minWidth:"720px"}} />
        
        {/*     */}
        <div>
          <Todaygrade />
        </div>

      </Container>
    </div>
  )
}

export default Homepage  // Homepage  
